import React, { useState, useEffect } from 'react';
import ArticlesList from '../components/ArticlesList';
import articles from './article-content';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlePage = ({ match })=> {
    const name = match.params.name;
    const article = articles.find((art) => art.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes:0, comments: [] });

    useEffect(() => {
       const fetchData = async() => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
       }

       fetchData();
    }, [name]);

    const otherArticles = articles.filter((art) => art.name !== name);
    if (!article) return <NotFoundPage />
    return (
        <>
            <h2>{article.title}</h2>
            <UpvoteSection name={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((para, key) => (
                <p key={key}>{para}</p>
            ))}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm name={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;